@import url('https://fonts.googleapis.com/css?family=Karla:400,700|Playfair+Display:400,700,900');

html,
body {
  height: 100%;
}

body {
  font-family: 'Karla', sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 14px;
}

body > div.wrapper {
  padding: 0px 100px;
  height: 100%;
}

.img {
  max-width: 100%;
  transition: all 0.3s ease-out 0s;
}

a,
.btn {
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
}

.display-inline {
  display: inline-block;
}

.btn {
  border-radius: 0px;
  font-size: 14px;
  padding: 0px 15px;
  height: 30px;
  line-height: 30px;
}

a:focus,
.btn:focus {
  text-decoration: none;
  outline: none;
}

a:focus,
a:hover {
  color: #464646;
  text-decoration: none;
}

a,
button,
input {
  outline: medium none;
  color: #464646;
}

.uppercase {
  text-transform: uppercase
}

.capitalize {
  text-transform: capitalize
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Playfair Display', serif;
  font-weight: normal;
  color: #363636;
  margin-top: 0px;
  font-style: normal;
  font-weight: 400;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit
}

h1 {
  font-size: 40px;
  font-weight: 500;
}

h2 {
  font-size: 35px;
}

h3 {
  font-size: 28px;
}

h4 {
  font-size: 22px;
}

h5 {
  font-size: 18px;
}

h6 {
  font-size: 16px;
}

ul {
  margin: 0px;
  padding: 0px;
}

li {
  list-style: none
}

p {
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  color: #626262;
  margin-bottom: 15px;
}

hr {
  margin: 60px 0;
  padding: 0px;
  border-bottom: 1px solid #eceff8;
  border-top: 0px;
}

hr.style-2 {
  border-bottom: 1px dashed #f10;
}

hr.mp-0 {
  margin: 0;
  border-bottom: 1px solid #eceff8;
}

hr.mtb-40 {
  margin: 40px 0;
  border-bottom: 1px solid #eceff8;
  border-top: 0px;
}

label {
  font-size: 15px;
  font-weight: 400;
  color: #626262;
}

*::-moz-selection {
  background: #4fc1f0;
  color: #fff;
  text-shadow: none;
}

::-moz-selection {
  background: #4fc1f0;
  color: #fff;
  text-shadow: none;
}

::selection {
  background: #4fc1f0;
  color: #fff;
  text-shadow: none;
}

.height-100vh {
  height: 100vh;
}

.mark,
mark {
  background: #4fc1f0 none repeat scroll 0 0;
  color: #ffffff;
}

span.tooltip-content {
  color: #00a9da;
  cursor: help;
  font-weight: 600;
}

.f-left {
  float: left
}

.f-right {
  float: right
}

.fix {
  overflow: hidden
}

.browserupgrade {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

/***************************
    transtion
****************************/

a.button::after,
a.button-small::after {
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
}
.share ul,
.share:hover ul {
  transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  -ms-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
}
a.button-border span,
a.button-border-white span,
input,
select,
textarea {
  -webkit-transition: all 0.5s ease-out 0s;
  -moz-transition: all 0.5s ease-out 0s;
  -ms-transition: all 0.5s ease-out 0s;
  -o-transition: all 0.5s ease-out 0s;
  transition: all 0.5s ease-out 0s;
}

/*************************
    Section title 1
***********************/

.section-title {} .section-title h1 {
                    margin: 0 0 15px 0;
                    line-height: 30px;
                  }
.section-title h2 {
  margin-top: 0px;
  line-height: 27px;
}
.section-title p {
  margin-bottom: 0px;
  font-size: 16px;
}
.section-title .title-line {
  padding: 10px 0
}
.section-title .title-line:before {
  background: #4FC1F0;
  content: "";
  display: block;
  height: 2px;
  margin: 0 auto;
  width: 220px;
}
.section-title .title-line:after {
  background: #4FC1F0;
  content: "";
  display: block;
  height: 6px;
  position: relative;
  top: -6px;
  margin: 0 auto;
  width: 60px;
}
/*************************
    section title 2
*************************/

.section-title-2 {
  margin-bottom: 60px
}
.section-title-2 h1 {
  margin-bottom: 0px
}
.section-title-2 h2 {
  margin-top: 0px;
  text-transform: capitalize;
  margin-bottom: 0px;
}
.section-title-2 p {
  margin-bottom: 0px;
  font-size: 16px;
}
.section-title-2 .title-line {
  padding: 20px 0 15px
}
.section-title-2 .title-line:before {
  background: #4FC1F0;
  content: "";
  display: block;
  height: 2px;
  margin: 0 auto;
  width: 100px;
}
.section-title-2 .title-line-2:before {
  background: #fff;
  content: "";
  display: block;
  height: 2px;
  margin: 0 auto;
  width: 100px;
}
/*************************
         button
*************************/

.button {
  border: 0 none;
  border-radius: 3px;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.03) inset;
  cursor: pointer;
  display: inline-block;
  margin-right: 10px;
  overflow: hidden;
  padding: 0;
  position: relative;
  background-color: #4FC1F0;
  color: #fff;
  font-size: 15px;
}
.button i {
  background: rgba(0, 0, 0, 0.09);
  display: block;
  float: left;
  padding: 14px;
  font-size: 16px;
  color: #fff;
  font-weight: normal;
  width: 50px;
  text-align: center;
}
.button span {
  display: block;
  float: left;
  position: relative;
  z-index: 2;
  padding: 11px 20px;
  font-size: 15px;
  color: #fff;
  font-weight: normal;
}
.button::after {
  background: rgba(0, 0, 0, 0.09) none repeat scroll 0 0;
  content: "";
  height: 100%;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 0;
  width: 0;
  z-index: 1;
}
.button:hover::after {
  width: 100%
}
