}
/*************************
       social-icon
*************************/

.social-icon ul {
  margin: 0;
  padding: 0;
}
.social-icon ul li {
  display: inline-block;
  list-style: none;
}
.social-icon ul li a {
  display: block;
  margin: 3px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  color: #fff;
  background: #4FC1F0;
  text-align: center;
  border-radius: 50%;
}
.social-icon ul li a:hover {
  background: #EB3B60;
}
.social-icon.socile-icon-style-2 ul li a {
  color: #fff;
  background: #323232;
  border-radius: 0;
}
.social-icon.socile-icon-style-2 ul li a:hover {
  background: #EB3B60;
}
.social-icon.socile-icon-style-3 ul li a {
  color: #fff;
  background: transparent;
  border: 1px solid #fff;
}
.social-icon.socile-icon-style-3 ul li a:hover {
  background: #EB3B60;
  border-color: #EB3B60;
}
